const findOne = async (req, res) => {
  try {
    const id = req.params.id;
    const carlendar = await CalendarAcademic.findOne({
      where: { id },
    });
    res.send({
      response_status: "SUCCESS",
      data: carlendar.dataValues,
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message:
        err.message || "Some error occurred while retrieving the carlendar.",
    });
  }
};

const update = async (req, res) => {
  try {
    const { id } = req.params;

    const result = await CalendarAcademic.update(req.body, {
      where: { id },
    });

    console.log("result", result);

    if (result != 1) {
      res.status(200).send({
        response_status: "ERROR",
        message: `(ID: ${id}) was not found.`,
      });
      return;
    }

    res.send({
      response_status: "SUCCESS",
      message: "Updated successfully",
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message: err.message || "Some error occurred while updating.",
    });
  }
};

const remove = async (req, res) => {
  try {
    const { id } = req.params;
    const result = await CalendarAcademic.destroy({ where: { id } });
    if (result !== 1) {
      res.status(200).send({
        response_status: "ERROR",
        message: `Log (ID: ${id}) was not found.`,
      });
      return;
    }

    res.send({
      response_status: "SUCCESS",
      message: "Deleted successfully",
    });
  } catch (err) {
    res.send({
      response_status: "ERROR",
      message: err.message || "Some error occurred while deleting.",
    });
  }
};

